#contents-body {
    background-color: #282c34;
    color: orange;
    margin: 0;
    text-align: left;
    display: inline-block;
    width: 100%;
    height: 100vh;
}

#head {
    margin: 0;
    text-align: center;
}

.contents_settings {
    padding-top: 0.25em;
    padding-bottom: 2em;
    margin-bottom: 2em;
    padding-left: 1em;

    font-family: Arial, Helvetica, sans-serif;
    background-color: black;

}

.headline {
    font-family: 'Times New Roman', Times, serif;
    color: aqua;
    font-size: 2em;
}

.text {
    text-align: left;
    font-family: "Hiragino Kaku Gothic ProN", Meiryo, "MS PGothic", Arial, Helvetica, sans-serif;
}

.long {
    overflow: scroll;
}
